.gadget {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 507px;
  width: 272px;
  box-sizing: border-box;
  padding: 24px;
  border: 1px solid #e2e6e9;
  margin-right: 16px;
  transition: 0.4s;
  text-decoration: none;
  font-family: Arial, sans-serif;
  color: black;

  &__button-favorite {
    position: relative;
    width: 40px;
    height: 40px;
    background-image: url("./favorite.svg");
    background-repeat: no-repeat;
    background-position: center;
    border: 1px solid black;
    box-sizing: border-box;
    transition: 0.4s;
    cursor: pointer;

    &:hover {
      border-color: gold;
    }

    &-check {
      position: absolute;
      top: -1px;
      left: -1px;
      height: 40px;
      width: 40px;
      background-repeat: no-repeat;
      background-position: center;
      transition: background-image 0.4s;
    }

    &-input {
      position: absolute;
      opacity: 0;

      &:checked ~ .item__button-favorite-check {
        background-image: url("./favorite-hover.svg");
      }
    }
  }

  &:hover {
    box-shadow: 5px 2px 5px rgba(0, 0, 0, 0.07);
  }

  &:hover .gadget__img {
    transform: scale(1.1);
    z-index: 1;
  }

  &:visited {
    color: inherit;
  }

  &__photo {
    height: 264px;
  }

  &__img {
    max-height: 208px;
    transition: 0.4s;
  }

  &__title {
    margin-top: 24px;
    display: flex;
    width: 100%;
    text-decoration: none;
    color: #313237;
    font-size: 130%;
    line-height: 21px;
    letter-spacing: 0;
  }

  &__price {
    box-sizing: border-box;
    font-family: Arial, sans-serif;
    display: flex;
    align-items: center;
    height: 44px;
    width: 100%;
    font-size: 22px;
    line-height: 28px;
    font-weight: bold;
    border-bottom: 1px solid #e2e6e9;
    color: #313237;

    &-value {
      color: #89939a;
      text-decoration-line: line-through;
      font-weight: 400;
      margin-left: 8px;
    }
  }

  &__button {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-top: 12px;

    &-add-to-cart {
      outline: none;
      border: none;
      font-family: Arial, sans-serif;
      box-sizing: border-box;
      width: 176px;
      height: 40px;
      background-color: #313237;
      transition: 0.4s;
      color: white;
      cursor: pointer;

      &:hover {
        box-shadow: 0 3px 13px rgba(23, 32, 49, 0.26);
      }
    }

    &-add-to-cart-selected {
      background-color: #313237;
      border: 1px solid black;
      color: white;
    }
  }
}

.description {
  font-size: 12px;
  line-height: 15px;
  color: black;
  display: flex;
  flex-direction: column;
  margin-top: 12px;
  font-weight: bolder;
  font-family: Arial, sans-serif;
  letter-spacing: 0;
  width: 100%;
}

.characteristic {
  &__span {
    display: flex;
    justify-content: space-between;
    width: 100%;

    & span {
      margin: 0;
      padding: 4px 0;
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
      color: #89939a;

      &:last-child {
        color: #313237;
      }
    }
  }
}
